.weather {
	position: relative;
	padding-top: 50px;
	background-image: linear-gradient(0deg, rgb(255, 102, 51) 0%, rgb(255, 102, 51) 24%, rgb(255, 204, 51) 100%);
	
	&__container {
		position: relative;
		z-index: 2;
	}
	
	&__main {
		padding-top: 40px;
		color: #fff;
		text-align: center;
	}
	
	&__temperature {

	}
	
	&__type {
		font-size: 22px;
		padding-top: 5px;
	}
	
	&__wind-moon {
		font-size: 16px;
		padding-top: 15px;
		text-align: center;
	}
	
	&__wind {
		display: inline-block;
		line-height: 24px;
		
		.svg-icon {
			width: 20px;
			height: 15px;
			margin-top: 4px;
		}
	}
	
	&__moon {
		display: inline-block;
		margin-left: 15px;
		line-height: 24px;
		
		span {
			padding-left: 4px;
		}
		
		.svg-icon {
			width: 23px;
			height: 23px;
		}
	}
}
