.weather-timing {
	color: #fff;
	padding: 15px 30px 30px;
	
	&__item {
		margin-top: 25px;
		line-height: 32px;
		.clearfix();
		
		.temperature {
			float: right;
		}
	}
	
	&__icon {
		float: right;
		margin-left: 20px;
		
		.svg-icon {
			width: 100%;
			height: 100%;
			max-width: 35px;
			max-height: 30px;
			vertical-align: middle;
		}
	}
	
	&__time {
		float: left;
	}
}
