.exchange-box {
	padding: 25px;
	
	&__item {
		font-size: 0;
		width: 200px;
		margin-left: auto;
		margin-right: auto;
		
		&:nth-of-type(n+2) {
			margin-top: 25px;
		}
	}
	
	&__label {
		display: inline-block;
		width: 75px;
		text-align: left;
		font-size: 22px;
		color: #333;
	}
	
	&__value {
		display: inline-block;
		font-size: 28px;
		color: #06c;
	}
}
