* {
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
}
body {
  font-size: 18px;
  font-weight: 400;
  font-family: "Open Sans", Arial, Helvetica, sans-serif;
}
a {
  color: #fff;
  text-decoration: none;
  border-bottom: 1px solid #ffffff;
}
@media (min-width: 992px) {
  a {
    color: #333;
    border-bottom: 1px solid #333333;
    transition: border .3s;
  }
  a:hover {
    border-bottom-color: rgba(51, 51, 51, 0);
  }
}
/* Blocks */
.banner {
  height: 100px;
  line-height: 100px;
  color: #fff;
  font-size: 24px;
  font-weight: 300;
  text-align: center;
  background-color: #ccc;
}
.dropdown {
  overflow-y: auto;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 100;
  padding: 25px;
  background-image: linear-gradient(0deg, #ff6633 0%, #ff6633 24%, #ffcc33 100%);
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s .3s ease, opacity .3s ease;
}
.dropdown.is-active {
  visibility: visible;
  opacity: 1;
  transition: opacity .3s ease;
}
.dropdown ul {
  margin: 0;
  padding: 0;
}
.dropdown li {
  font-size: 18px;
  list-style-type: none;
}
.dropdown li:nth-of-type(n+2) {
  margin-top: 30px;
}
.dropdown li.is-active .svg-icon {
  opacity: 1;
}
.dropdown a {
  position: relative;
  border: none;
}
.dropdown .svg-icon {
  position: absolute;
  top: 0;
  right: -30px;
  opacity: 0;
  transition: opacity .3s;
}
.exchange {
  padding-top: 30px;
}
.exchange__title {
  font-size: 24px;
  text-align: center;
  color: #000;
}
.exchange__bank {
  margin-top: 10px;
  font-size: 16px;
  text-align: center;
  color: #666;
}
.exchange-box {
  padding: 25px;
}
.exchange-box__item {
  font-size: 0;
  width: 200px;
  margin-left: auto;
  margin-right: auto;
}
.exchange-box__item:nth-of-type(n+2) {
  margin-top: 25px;
}
.exchange-box__label {
  display: inline-block;
  width: 75px;
  text-align: left;
  font-size: 22px;
  color: #333;
}
.exchange-box__value {
  display: inline-block;
  font-size: 28px;
  color: #06c;
}
.exchange-chart {
  padding: 20px;
}
.exchange-type__bookmarks {
  border-bottom: 2px solid #ddd;
  padding-left: 20px;
}
.exchange-type__item {
  display: inline-block;
  border: 2px solid rgba(221, 221, 221, 0);
  color: #999;
  font-size: 14px;
  border-bottom: none;
  padding: 10px 14px;
  margin-bottom: -2px;
  border-radius: 3px 3px 0 0;
  transition: border .3s, color .3s;
}
.exchange-type__item.is-active {
  color: #06c;
  border: 2px solid #dddddd;
  border-bottom-color: #fff;
}
.header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 5;
  padding-top: 20px;
}
.header__city {
  font-size: 18px;
  font-weight: 600;
}
.header__region {
  text-align: right;
}
.header__region a {
  font-size: 16px;
  display: inline-block;
  height: 30px;
  padding: 0 6px;
  line-height: 26px;
  border-radius: 3px;
  border: 2px solid #fff;
}
.svg-icon {
  width: 16px;
  height: 16px;
  display: inline-block;
  vertical-align: top;
  fill: currentColor;
}
.temperature {
  display: inline-block;
  position: relative;
  font-size: 60px;
  font-weight: 300;
}
.temperature::after {
  position: absolute;
  top: 0;
  left: 100%;
  content: "°";
}
.temperature--small {
  font-size: 22px;
  font-weight: 600;
}
.weather {
  position: relative;
  padding-top: 50px;
  background-image: linear-gradient(0deg, #ff6633 0%, #ff6633 24%, #ffcc33 100%);
}
.weather__container {
  position: relative;
  z-index: 2;
}
.weather__main {
  padding-top: 40px;
  color: #fff;
  text-align: center;
}
.weather__type {
  font-size: 22px;
  padding-top: 5px;
}
.weather__wind-moon {
  font-size: 16px;
  padding-top: 15px;
  text-align: center;
}
.weather__wind {
  display: inline-block;
  line-height: 24px;
}
.weather__wind .svg-icon {
  width: 20px;
  height: 15px;
  margin-top: 4px;
}
.weather__moon {
  display: inline-block;
  margin-left: 15px;
  line-height: 24px;
}
.weather__moon span {
  padding-left: 4px;
}
.weather__moon .svg-icon {
  width: 23px;
  height: 23px;
}
.weather-animations {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 0;
}
.weather-animations__city {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  height: 166px;
  opacity: .1;
  background: url(../images/city-pattern.png) 0 0 repeat-x;
}
.weather-timing {
  color: #fff;
  padding: 15px 30px 30px;
}
.weather-timing__item {
  margin-top: 25px;
  line-height: 32px;
}
.weather-timing__item:before,
.weather-timing__item:after {
  content: " ";
  display: table;
}
.weather-timing__item:after {
  clear: both;
}
.weather-timing__item .temperature {
  float: right;
}
.weather-timing__icon {
  float: right;
  margin-left: 20px;
}
.weather-timing__icon .svg-icon {
  width: 100%;
  height: 100%;
  max-width: 35px;
  max-height: 30px;
  vertical-align: middle;
}
.weather-timing__time {
  float: left;
}
