.dropdown {
	overflow-y: auto;
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	z-index: 100;
	padding: 25px;
	background-image: linear-gradient(0deg, rgb(255, 102, 51) 0%, rgb(255, 102, 51) 24%, rgb(255, 204, 51) 100%);
	
	visibility: hidden;
	opacity: 0;
	transition: visibility 0s .3s ease, opacity .3s ease;
	
	&.is-active {
		visibility: visible;
		opacity: 1;
		transition: opacity .3s ease;
	}

	ul {
		margin: 0;
		padding: 0;
	}

	li {
		font-size: 18px;
		list-style-type: none;

		&:nth-of-type(n+2) {
			margin-top: 30px;
		}
		
		&.is-active {
			.svg-icon {
				opacity: 1;
			}
		}
	}
	
	a {
		position: relative;
		border: none;
	}
	
	.svg-icon {
		position: absolute;
		top: 0;
		right: -30px;
		opacity: 0;
		transition: opacity .3s;
	}
}
