.exchange-type {
	
	&__bookmarks {
		border-bottom: 2px solid #ddd;
		padding-left: 20px;
	}
	
	&__item {
		display: inline-block;
		border: 2px solid rgba(221, 221, 221, 0);
		color: #999;
		font-size: 14px;
		border-bottom: none;
		padding: 10px 14px;
		margin-bottom: -2px;
		border-radius: 3px 3px 0 0;
		transition: border .3s, color .3s;
		
		&.is-active {
			color: #06c;
			border: 2px solid rgba(221, 221, 221, 1);
			border-bottom-color: #fff;
		}
	}
}
