.header {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	z-index: 5;
	padding-top: 20px;
	
	&__city {
		font-size: 18px;
		font-weight: 600;
	}
	
	&__region {
		text-align: right;
		
		a {
			font-size: 16px;
			display: inline-block;
			height: 30px;
			padding: 0 6px;
			line-height: 26px;
			border-radius: 3px;
			border: 2px solid #fff;
		}
	}
}
